/* Estilos globales */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  width: 95%;
  margin: 0 auto;
}

body {
  background: rgb(219, 231, 200);
  font-family: "Roboto", sans-serif;
  min-height: 100vh;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  margin: 0;
}

/* Estilos de la navegaci√≥n superior */
.navigation {
  background-color: #bfd695;
  padding: 1% 0;
}

.navigation-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.navigation-item {
  margin: 0 15px;

}


.navigation-item a {
  text-decoration: none;
  color: #333;
  padding: 8px 16px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.navigation-item:hover {
  background-color: #ddd;
  cursor: pointer;
}

